@mixin respond($breakpoint) {
  @if $breakpoint == phone {
      @media only screen and (max-width: 47.9375em) { @content };    //767px
  }
  @if $breakpoint == tab-port {
      @media only screen and (max-width: 61.9375em) { @content };     //991px
  }
  @if $breakpoint == tab-land {
      @media only screen and (max-width: 74.9375em) { @content };    //1199px
  }
  @if $breakpoint == big-desktop {
      @media only screen and (min-width: 75em) { @content };    // 1200px +
  }
}